// 抛物线计算
function bezier(pots, amount) {
	var pot;
	var lines;
	var ret = [];
	var points;
	for (var i = 0; i <= amount; i++) {
		points = pots.slice(0);
		lines = [];
		while ((pot = points.shift())) {
			//将points数组中第一个元素从其中删除，并返回第一个元素的值。
			if (points.length) {
				//如果是有值的话
				lines.push(pointLine([pot, points[0]], i / amount));
			} else if (lines.length > 1) {
				points = lines;
				lines = [];
			} else {
				break;
			}
		}
		ret.push(lines[0]);
	}

	function pointLine(points, rate) {
		var pointA, pointB, pointDistance, xDistance, yDistance, tan, radian, tmpPointDistance;
		var ret = [];
		pointA = points[0]; //点击
		pointB = points[1]; //中间
		xDistance = pointB.x - pointA.x;
		yDistance = pointB.y - pointA.y;
		pointDistance = Math.pow(Math.pow(xDistance, 2) + Math.pow(yDistance, 2), 1 / 2);
		tan = yDistance / xDistance;
		radian = Math.atan(tan);
		tmpPointDistance = pointDistance * rate;
		ret = {
			x: pointA.x + tmpPointDistance * Math.cos(radian),
			y: pointA.y + tmpPointDistance * Math.sin(radian)
		};
		return ret;
	}
	return {
		bezier_points: ret
	};
}
export default {
	data() {
		return {
			StatusAddNav:this.StatusAddNav,
			// 购物车小球动画start
			hide_good_box: false,
			bus_x: 0,
			bus_y: 0,
			finger: {},
			busPos: {},
			linePos: [],
			timer: null,
			carNum: 0, //加入到购物车的总个数
			// 购物车小球动画end
			
			isKeep: false, //收藏
			showCategoryIndex: 0,
			//分类列表
			categoryList: [],
			//商品列表
			goodsList: [],
			//控制价钱升降排序标记
			priceOrder: 0,
			//控制销量升降排序标记
			salesOrder: 0,
			//控制综合升降排序标记
			allOrder: 0,
			//当前分类的id
			sortCurrentId: 0
		};
	},
	onLoad() {
		let hh = 0;
		const that = this;
		that.busPos['x'] = 330; //购物车的位置






		wx.getSystemInfo({
			success: function(res) {
				hh = res.windowHeight;
				that.busPos['y'] = hh - 83;
			}
		});

		this.getCategoryList();
	},

	methods: {
		// 加入购物车start
		touchOnGoods(e, id) {
			console.log('点击的坐标:', id);
			if (this.timer) {
				// 清除一下动画
				clearInterval(this.timer);
			}
			this.finger = {};
			var topPoint = {};
			this.finger['x'] = e.target.x; //点击的位置
			this.finger['y'] = e.target.y;
			if (this.finger['y'] < this.busPos['y']) {
				topPoint['y'] = this.finger['y'] - 150;
			} else {
				topPoint['y'] = this.busPos['y'] - 150;
			}
			topPoint['x'] = Math.abs(this.finger['x'] - this.busPos['x']) / 2;

			if (this.finger['x'] > this.busPos['x']) {
				topPoint['x'] = (this.finger['x'] - this.busPos['x']) / 2 + this.busPos['x'];
			} else {
				//
				topPoint['x'] = (this.busPos['x'] - this.finger['x']) / 2 + this.finger['x'];
			}
			//调用全局的方法进行计算x和y变化的数据,执行次数是30次
			this.linePos = bezier([this.busPos, topPoint, this.finger], 30);
			this.startAnimation(id);
		},
		startAnimation(id) {
			var index = 0,
				that = this,
				bezier_points = that.linePos['bezier_points'];
			that.hide_good_box = true;
			that.bus_x = that.finger['x']; //当点击完之后小球复位到点击的位置,随意定时更新由bezier生产的数据
			that.bus_y = that.finger['y'];
			var len = bezier_points.length;
			index = len; //有多少组数据，一直执行到数据index为
			that.timer = setInterval(function() {
				//将先钱计算得到的数据展示出来,以0.025s的速度展示,实现飞进去的动画
				index--;
				that.bus_x = bezier_points[index]['x'];
				that.bus_y = bezier_points[index]['y'];
				if (index < 1) {
					clearInterval(that.timer);
					that.hide_good_box = true;
					that.addGoodToCar(id);
				}
			}, 25);
		},
		//添加商品到购物车中
		addGoodToCar(gId) {
			let that = this;
			//是否登入
			if (that.isLogin()) {
				//获取该商品的信息
				that.baseGet(
					that.U({
						c: 'store_api',
						a: 'details',
						q: {
							id: gId
						}
					}),
					function(res) {
						//添加到购物车中
						that.basePost(
							that.U({ c: 'auth_api', a: 'set_cart' }),
							{
								productId: gId,
								merId: res.data.mer_id,
								cartNum: 1
							},
							function(res) {
								that.carNum++;
								that.Tips({ title: '添加成功,在购物车等亲' });
							},
							function(res) {
								that.Tips({ title: '服务器开小差了，请稍后在试~' });
							}
						);
					},
					function(res) {
						console.log(res);
					},
					true
				);
			}
		},
		//改变综合排序
		changeAllRank() {
			this.allOrder = this.allOrder == 2 ? 0 : this.allOrder + 1;
			this.getSortGoods();
		},
		//改变销量排序
		changeSalesRank() {
			this.salesOrder = this.salesOrder == 2 ? 0 : this.salesOrder + 1;
			this.getSortGoods();
		},
		//改变价格排序
		changePriceRank() {
			this.priceOrder = this.priceOrder == 2 ? 0 : this.priceOrder + 1;
			this.getSortGoods();
		},
		//获取分类列表
		getCategoryList() {
			var that = this;
			that.baseGet(
				that.U({ c: 'store_api', a: 'get_pid_cate' }),
				function(res) {
					//获取分类列表中的第一个id来获取goods数据
					that.showCategory(0, res.data[0].id);
					that.categoryList = res.data;
				},
				function(res) {
					console.log(res);
				},
				true
			);
		},
		//收藏
		keep() {
			this.isKeep = this.isKeep ? false : true;
		},

		//获取指定类别的商品
		getSortGoods() {
			let that = this;
			that.goodsList = [];
			that.baseGet(
				that.U({
					c: 'store_api',
					a: 'get_product_list',
					q: {
						cid: that.sortCurrentId,
						keyword: '',
						priceOrder: that.priceOrder == 0 ? '' : that.priceOrder == 1 ? 'desc' : 'asc',
						salesOrder: that.salesOrder == 0 ? '' : that.salesOrder == 1 ? 'desc' : 'asc',
						news: '',
						page: '',
						limit: ''
					}
				}),
				function(res) {
					that.goodsList = res.data;
					console.log(that.goodsList);
				},
				function(res) {
					console.log(res);
				},
				true
			);
		},
		//分类切换显示
		showCategory(index, id) {
			this.showCategoryIndex = index;
			this.sortCurrentId = id;
			this.getSortGoods();
		},
		// 搜索商品
		search(e) {
			uni.navigateTo({
				url: '/pages/list/search/search'
			});
		},
		// 商品详情
		cart_detail(){
			uni.switchTab({
				url:'/pages/tabber/shopping_cart/shopping_cart'
			})
		},
		goodeDetails(e) {
			uni.navigateTo({
				url: '/pages/list/goode_details/goode_details?id=' + e.currentTarget.dataset.id
			});
		}
	}
};
